let pressedMap = {

}
let actionsPreviousTick = {

}

const up = (actionData) => {
    actionData.move.y -= 1
}
const left = (actionData) => {
    actionData.move.x -= 1
}
const right = (actionData) => {
    actionData.move.x += 1
}
const down = (actionData) => {
    actionData.move.y += 1
}
const keyActionMap = {
    'w': 'up',
    'a': 'left',
    'd': 'right',
    's': 'down',
    'ArrowUp': 'up',
    'ArrowLeft': 'left',
    'ArrowRight': 'right',
    'ArrowDown': 'down',
    'Escape': 'toggleMainMenu'
}
const actionMap = {
    'up': {f: up},
    'left': {f: left},
    'right': {f: right},
    'down': {f: down},
    'toggleMainMenu': {f: (actionData) => actionData.toggleMainMenu = true}
}

const controls = {
    keydown: (event) => {
        pressedMap[event.key] = true
    },
    keyup: (event) => {
        pressedMap[event.key] = false
    },
    // use a call back for running through the actions? Pass the different action functions to outside?
    // Or use some static class to manage certain global things?
    update: () => {
        if (!document.hasFocus()) pressedMap = {}
        let actions = {}, previousActions = {}
        for (let pressed in pressedMap) {
            let action = keyActionMap[pressed]
            if (pressedMap[pressed]) {
                if (!actionsPreviousTick[action] || actionMap[action].continuos) actions[action] = true
                else previousActions[action] = true
            }
        }
        let actionData = {
            move: {x: 0, y: 0}
        }

        for (let action in actions) {
            actionMap[action].f(actionData)
        } 
        actionsPreviousTick = {...previousActions, ...actions}
        return actionData
    }
}

export default controls