import * as THREE from "three"
import carModel from '../../models/voxel_car/car.glb'

class WorldStrip {
    constructor (id, row, type, scene, objectNumber, carDirection) {
        this.id = id
        this.row = row
        this.type = type
        this.scene = scene
        this.objectNumber = objectNumber

        this.objects = []

        if (type === "road") {
            const geometry = new THREE.PlaneGeometry(100, 1);
            const texture = new THREE.TextureLoader().load("road.jpeg");
            const material = new THREE.MeshStandardMaterial({ map: texture, depthWrite: false });
            const mesh = new THREE.Mesh(geometry, material);
            mesh.position.z = -row
            mesh.position.y = -0.01
            this.carDirection = carDirection

            scene.add(mesh)
        }

        else if (type === "safe") {
            const geometry = new THREE.PlaneGeometry(100, 1);
            const texture = new THREE.TextureLoader().load("safe.jpg");
            const material = new THREE.MeshStandardMaterial({ map: texture, depthWrite: false });
            const mesh = new THREE.Mesh(geometry, material);
            mesh.position.z = -row
            mesh.position.y = -0.01
            scene.add(mesh)
        }
    }

    update() {
        this.objects.forEach(car => {
            car.position.x += 0.04 * this.carDirection
            if (car.position.x > 17) {
                car.position.x = -17
            }
            if (car.position.x < -17) {
                car.position.x = 17
            }
        })
    }

    async load(loadModel, loader) {
        for (let i = 0; i < this.objectNumber; i++) {
            if (this.type === "road") {
                try {
                    let car = await loadModel(loader, carModel)
                    car.scale.set(0.08, 0.08, 0.08)
                    car.position.x = this.newCarPosition(this.objects)
    
                    car.rotation.y = this.carDirection === 1 ? Math.PI : 0;
                    car.position.z = this.carDirection === 1 ? -this.row + 0.5 : -this.row - 0.5;
                    this.objects.push(car)
                    this.scene.add( car );
                } catch (e) {
                    console.log(e)
                }
            }
            else if (this.type === "safe") {
                let object
                /* Math.floor(Math.random()) === 0 ?  */object = this.createTree(0.3, 1, 0.7, 2) //: object = createWater()
                
                object.position.x = Math.random() * 20 - 10
                object.position.z = -this.row
                this.objects.push(object)
                this.scene.add( object )
                
            }
        }
    }

    newCarPosition (carList) {
        let x
        let existing = true
        while (existing) {
            x = Math.random() * 17 * 2 - 17
            existing = carList.find(c => x > c.position.x - 3 && x < c.position.x + 3 )
        }
        return x
    }

    createTree(cylinderRadius, cylinderHeight, baseConeRadius, coneHeight) {

        const cylinderGeometry = new THREE.CylinderGeometry(cylinderRadius, cylinderRadius, cylinderHeight, 32);

        const cylinderTexture = new THREE.TextureLoader().load("log.jpg");

        const cylinderMaterial = new THREE.MeshStandardMaterial({ map: cylinderTexture });

        const cylinder = new THREE.Mesh(cylinderGeometry, cylinderMaterial);

        // Move base of the cylinder to y = 0

        cylinder.position.y = cylinderHeight / 2.0;

        cylinder.castShadow = true;
        cylinder.receiveShadow = true;			

        // Cone

        const coneGeometry = new THREE.ConeGeometry(baseConeRadius, coneHeight, 32);

        const coneTexture = new THREE.TextureLoader().load("tree_top.jpg");
        
        const coneMaterial = new THREE.MeshStandardMaterial({ map: coneTexture });

        const cone = new THREE.Mesh(coneGeometry, coneMaterial);

        // Move base of the cone to the top of the cylinder

        cone.position.y = cylinderHeight + coneHeight / 2.0;

        cone.castShadow = true;
        cone.receiveShadow = true;						

        // Tree

        var tree = new THREE.Group();

        tree.add(cylinder);

        tree.add(cone);

        return tree;
    }
}
export default WorldStrip