import { useEffect, useState } from "react";

import game from "./game";

function App() {
  let [status, setStatus] = useState('start')
  let [points, setPoints] = useState(0)
  let [modifyStatus, setModifyStatus] = useState()

  useEffect(() => {
    async function loadGame () {
      let obj = await game.start(setStatus, setPoints)
      let interval = obj.interval
      setModifyStatus({modify: obj.modifyStatus})

      return () => {
        clearInterval(interval)
      }
    }
    loadGame()
  }, [])
  return (
    <div className="App">
      {status === 'start' ?
        <div className="start-btn" onClick={() => modifyStatus && modifyStatus.modify('running')}>
          START GAME 
        </div> 
      : ''}
      {status === 'ended' ?
        <div className="start-btn" onClick={() => modifyStatus && modifyStatus.modify('running')}>
          You Lost! START GAME
        </div> 
      : ''}
      <div className="score">{points}</div>
    </div>
  );
}

export default App;
