import controls from "./controls"
import WorldStrip from "./models/worldStrip"
import * as THREE from "three"
import { GLTFLoader } from "../lib/GLTFLoader"
import chickenModel from '../models/Crossy_Road_Chicken.glb'

let chicken
let chickenRow = 0
let maxRow = 0
let generatedRows = 0
let camera
let strips = []
let stripType = ["road", "safe"]

let status = 'start'

const executeActions = (actionData, setPoints) => {
    if (!chicken || status !== 'running') return

    // Conditions to limit the chicken positions (eg. not going further left/right, not going backwards in the beginning or against trees)
    if (strips.find(s => s.objects.find(o => Math.round(o.position.x) === Math.round(chicken.position.x + actionData.move.x) && o.position.z === (chicken.position.z + actionData.move.y)))) return
    if (chicken.position.x + actionData.move.x > -10 && chicken.position.x + actionData.move.x < 10) {
        chicken.position.x += actionData.move.x
    }
    if (chicken.position.z + actionData.move.y < 3) {
        chicken.position.z += actionData.move.y
        camera.position.z += actionData.move.y
        chickenRow += actionData.move.y
        maxRow = Math.max(-maxRow, -chickenRow)
        setPoints(maxRow)
    }
    if (actionData.move.y) {
        if (actionData.move.y > 0) {
            chicken.rotation.z = Math.PI / 2
        } else {
            chicken.rotation.z = - Math.PI / 2
        }
    } else if (actionData.move.x) {
        if (actionData.move.x > 0) {
            chicken.rotation.z = 0
        } else {
            chicken.rotation.z = Math.PI
        }
    }
}


const loadModel = async (loader, model) => {
    return new Promise((resolve, reject) => {
        loader.load( model, function ( glb ) {
            resolve(glb.scene)
          }, undefined, function ( error ) {
            reject(error)
        });
    })
}

const collision = (chicken, car) => {

    let carBox = new THREE.Box3().setFromObject(car)
    let chickenBox = new THREE.Box3().setFromObject(chicken)

    return chickenBox.min.x < carBox.max.x && chickenBox.max.x > carBox.min.x
}

const generateStrips = async (scene, loadModel, loader) => {
    const minRows = 10
    const preGeneratedRows = 20

    if (generatedRows - minRows <= maxRow) {
        let numberToGenerate = preGeneratedRows - (generatedRows - chickenRow)
        for (let i = 0; i < numberToGenerate; i++) {
            let worldStrip = new WorldStrip("firstStrip", ++generatedRows, stripType[Math.floor(Math.random()*2)], scene, Math.floor(Math.random()*4 + 2), Math.floor(Math.random()*2) === 1 ? 1 : -1)
            await worldStrip.load(loadModel, loader)
            strips.push(worldStrip)
        }
    }
}

const game = {
    start: async (setStatus, setPoints) => {

        window.addEventListener('keydown', controls.keydown)
        window.addEventListener('keyup', controls.keyup)

        let interval = setInterval(() => {
            let actionData = controls.update()
            executeActions(actionData, setPoints)
        }, 1000 / 60)

        var scene = new THREE.Scene();
        
        scene.background = new THREE.Color(0x478225)
        var aspect = window.innerWidth / window.innerHeight;
        var d = 5;
        camera = new THREE.OrthographicCamera( - d * aspect, d * aspect, d, - d, 1, 1000 );
        var renderer = new THREE.WebGLRenderer();
        renderer.setSize( window.innerWidth, window.innerHeight );
        document.body.appendChild( renderer.domElement );
       
        camera.position.x = 20;
        camera.position.y = 20;
        camera.position.z = 20;
        camera.lookAt( scene.position );
        const loader = new GLTFLoader();

        try {
            chicken = await loadModel(loader, chickenModel)
            chicken.scale.set(0.75, 0.75, 0.75)
            chicken.rotation.x = Math.PI/2;
            chicken.rotation.z = -Math.PI/2;
            scene.add( chicken );
        } catch (e) {
            console.log(e)
        }

        
        const light = new THREE.AmbientLight( 0xffffff ); // soft white light
        scene.add( light );
        var animate = function () {
            requestAnimationFrame( animate );
            strips.forEach(strip => {
                if (strip.type === "road") {
                    strip.update()
                    if (strip.row === -chickenRow) {
                        strip.objects.forEach(object => {
                            if (collision(chicken, object)) {
                                chicken.position.x = 0
                                chicken.position.y = 0
                                chicken.position.z = 0
                                chicken.rotation.z = -Math.PI / 2
                                chickenRow = 0
                                maxRow = 0
                                camera.position.x = 20
                                camera.position.x = 20
                                camera.position.z = 20
                                status = 'ended'
                                setStatus('ended')
                            }
                        })
                    }
                }
            })
            generateStrips(scene, loadModel, loader)
            renderer.render( scene, camera );
        };
        animate();

        return {interval, modifyStatus: (newStatus) => {
            setPoints(0)
            setStatus(newStatus)
            status = newStatus
        }}
    }
}

export default game